






























































































import { Component, Vue } from 'vue-property-decorator';
import { mask } from 'vue-the-mask';
import { cpf } from 'cpf-cnpj-validator';
import { onlyNumbers } from '@/utils';
import { VForm } from '@/models/v-form';
import User from '@/models/user';

@Component({
  components: {
  },
  directives: {
    mask,
  },
})
export default class UserForm extends Vue {
  formModels = new User();

  formRules = {
    Nome: [(v: string) => v && v.length >= 7],
    CPF: [(v: string) => cpf.isValid(v, true)],
    Telefone: [(v: string) => v && (/\(\d{2,}\) \d{4,}-\d{4}/.test(v))],
    Email: [(v: string) => v && (/.+@.+/.test(v))],
    Senha: [(v: string) => v && v.length >= 5],
    Categoria: [(v: number) => !!v],
  };

  formUtils = {
    Senha: {
      showValue: false,
    },
    Categoria: {
      items: [
        {
          text: 'Administrador',
          value: 1,
        },
        {
          text: 'Secretaria e afins',
          value: 2,
        },
        {
          text: 'Operaes acadmicas',
          value: 3,
        },
      ],
    },
  };

  isFormValid = false;

  get formRef() {
    return this.$refs.form as VForm;
  }

  resetForm() {
    this.formRef.reset();
  }

  registerUser() {
    const value = this.formModels;
    value.CPF = onlyNumbers(value.CPF);
  }
}
