export default class User {
  Id: number;

  Nome: string;

  CPF: string;

  Telefone: string;

  Email: string;

  Senha?: string;

  Categoria?: number;

  Acoes?: string[];

  constructor() {
    this.Id = 0;
    this.Nome = '';
    this.CPF = '';
    this.Telefone = '';
    this.Email = '';
    this.Senha = '';
    this.Categoria = 0;
    this.Acoes = [];
  }
}
